import { cloudFunction } from '@/utils/request'
import { mapGetters } from 'vuex'

export default {
  props: {
    productList: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data () {
    return {
      showModalStatus: false,
      animationData: "",
      product: {}
    }
  },
  computed: {
    ...mapGetters('user', ['userInfo', 'isAuth'])
  },
  methods: {
    showModal: function (index) {
      this.product = this.productList[index]
      // 显示遮罩层
      // 创建动画实例 
      let animation = uni.createAnimation({
        duration: 200,
        timingFunction: "linear",
        delay: 0
      })
      animation.translateY(480).step()
      this.animationData = animation.export();
      this.showModalStatus = true
      setTimeout(function () {
        animation.translateY(0).step()
        this.animationData = animation.export()
      }.bind(this), 200)
    },
    hideModal: function () {
      this.showModalStatus = false
    },
    async handleExchange (product) {
      if (this.isAuth) {
        const res = await cloudFunction('handleExchange', { userId: this.userInfo.userId, product })
        const [err, data] = res
        if (data.code === 0) {
          uni.showToast({
            title: data.data.message,
            icon: data.data.success ? 'success' : 'none',
            duration: 2000
          })
          if (data.data.success) {
            this.$emit('success')
          }
        }
      } else {
        uni.showModal({
          title: '提示',
          content: 'RUC社团说需要获取QQ授权!',
          success (res) {
            if (res.confirm) {
              qq.reLaunch({ url: `/pages/user/login` })
            } else if (res.cancel) {
              console.log('用户点击取消')
            }
          }
        })
      }

    }
  }
}
